.clockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 49px;
}

.backArrow {
    position: unset;
    margin-right: 1100px;
    color: #9b9b9b;
}

.clockTitle {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin-bottom: 59px;
    font-size: 130px;
    color: #969595;
}

.timeBox {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 198px;
    width: 163px;
    background-color: #525252;
    color: #fff;
    font-size: 120px;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
}

.DefaultClockBlock {
    display: flex;
    gap: 20px;
}

.DefaultClockAndDateContainer {
    display: flex;
    flex-direction: column;
}

.body {
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.dateTimeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    text-align: center;
    color: white;
    font-size: 24px;
}



.stopWatchTitle {
    font-size: 36px;
    color: white;
    margin-bottom: 20px;
}
