
.clockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 291px;
    height: 279px;
    text-align: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #262626;
    align-content: center;
}

.numbersContainer {
    width: 249px;
    height: 239px;
    text-align: center;
    display: flex;
    align-items: center;
    border: solid 1px #262626;
    border-radius: 60%;
    margin: 5px;
    background: #424242;
    align-content: center;
    justify-content: center;
}

.linkButtons{
    display: flex;
    justify-content: space-between;
    min-width: 80%;
}

.backArrow {

    color: #9b9b9b;
}

.stopWatchTitle {
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin-bottom: 59px;
    font-size: 130px;
    color: #969595;
}

.clockElements {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 60px;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    width: 44px;
}


.body {
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 295px;
    margin-top: 75px;
}

.startButton {
    height: 87px;
    width: 85px;
    background-color: rgba(49, 134, 35, 0.25);
    color: white;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.startButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.stopButton {
    height: 87px;
    width: 85px;
    background-color: rgba(194, 84, 60, 0.25);
    color: #ffff;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.stopButton:hover {
    opacity: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}


.resetButton {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #fff;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

.resetButton:disabled {
    height: 87px;
    width: 85px;
    background-color: rgba(0, 0, 0, 0.25);
    color: #757474;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease, box-shadow 0.3s ease;
}

@media (max-width: 914px) {
    .linkButtons{
        min-width: 100%;
    }

    .stopWatchTitle{
        margin-bottom: 0;
    }
    .buttons{
        margin-top: 0;
    }
}

@media (max-width: 573px) {
    .stopWatchTitle{
        font-size: 80px;
    }
    .buttons{
        gap: 100px;
    }
}